
let totalCount = 0;
import XHeader from 'vux/src/components/x-header/index.vue'

import VideoListWrap from "@/components/common/VideoListWrap";
import { VideoCreditList, SeourceCreatedList } from "@/servers/api";
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import { setTimeout } from "timers";
export default {
name: "",
props: [""],
data() {
return {
scrollbarObj: {
fade: true
},
pullDownRefreshObj: {
threshold: 70,
stop: 40
},
pullUpLoadObj: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多数据了"
}
},
startY: 0, // 纵轴方向初始化位置
scrollToX: 0,
scrollToY: 0,
scrollToTime: 700,
items: [],
commentCount: 99,
contentList: [],
page: 1,
totalNum: 0,
noData: false
};
},

components: {
XHeader,
VideoListWrap,
InlineLoading
},

computed: {
contentSty() {
return { height: document.documentElement.clientHeight - 55 + "px" };
}
},

beforeMount() {},

mounted() {
this.onPullingDown();
},

methods: {
getVideoList() {
return new Promise(resolve => {
let arr = [];
SeourceCreatedList({
type: 8,
page: this.page
})
.then(res => {
console.log(res);
if (res.result === 1) {
totalCount = res.data.totalofnum;
this.totalNum = res.data.totalofnum;
this.noData =
res.data.currentpage === res.data.totalpage ? true : false;
setTimeout(() => {
if (res.data.comment.length) {
arr = res.data.comment;
resolve(res.data.comment);
} else {
this.$nextTick(() => {
this.$refs.scroll.forceUpdate(false);
});
}
}, 1000);
}
})
.catch(err => {
console.log(err);
});
});
},

scrollTo() {
this.$refs.scroll.scrollTo(
this.scrollToX,
this.scrollToY,
this.scrollToTime
);
},
onPullingDown() {
totalCount = 0;
this.page = 1;
this.getVideoList().then(res => {
this.contentList = res;
setTimeout(() => {
this.$nextTick(() => {
this.$refs.scroll.forceUpdate(true);
});
}, 1000);
});
},
onPullingUp() {
this.page += 1;
this.getVideoList().then(res => {
this.contentList = this.contentList.concat(res);
if (this.contentList.length < totalCount) {
this.$refs.scroll.forceUpdate(true);
} else {
this.$refs.scroll.forceUpdate(false);
}
});
},
releaseContent() {
this.$router.push("/releasevideo??branch=8");
},
back() {
this.$router.goBack();
}
},
beforeDestroy() {},
watch: {}
};



